table.price-table {
    border-spacing: 0;
    border-collapse: collapse;
    table-layout: fixed;
    width: 100%;

    td,
    th {
        border: 1px solid #e7e7e7;
        padding: 0;
        word-wrap: break-word;

        @include breakup-breakpoint('thin') {
            // 320px
            padding: 5px;
        }
        @include breakup-breakpoint('thinish') {
            // 480px
            padding: 10px;
            word-wrap: normal;
        }
    }

    tbody tr {
        td {
            color: $red1;
            font-weight: 600;
        }

        :first-child {
            color: black;
            font-weight: 400;
        }
    }
}
