.side-menu {
    li {
        list-style-type: none;
        display: inline-block;
    }

    display: none;
    @include breakup-breakpoint('thinish_extended_2') {
        display: block;
    }

    margin-bottom: 20px;
    text-align: center;

    @include breakup-breakpoint('medium') {
        // 768px
        text-align: left;
        //border-right: 3px solid $red1;
        //padding-right: 20px;
        li {
            display: block;
        }
    }

    a.side-menu-item {
        padding: 10px;
        border: none;
        display: block;

        @include breakup-breakpoint('thinish_extended_2') {
            display: inline-block;
        }
        @include breakup-breakpoint('medium') {
            // 768px
            display: block;
        }

        &:hover span {
            border-bottom: 1px dotted #858687;
        }

        font-size: 1.2em;
        margin-bottom: 10px;

        background: white;
        color: $red1;
        text-decoration: none;

        &:focus {
            background: $red1;
            color: white;
            span {
                border-bottom: 1px solid white;
            }
        }

        &.selected {
            background: $red1;
            color: white;
            position: relative;

            span {
                border: none;
            }

            &::before {
                content: ' ';
                position: absolute;
                width: 0;
                height: 0;

                bottom: -5px;
                left: 50%;
                margin-left: -5px;
                border-left: 5px solid transparent;
                border-right: 5px solid transparent;
                border-top: 5px solid $red1;

                @include breakup-breakpoint('medium') {
                    left: auto;
                    right: -5px;
                    bottom: auto;
                    top: 50%;
                    margin-left: 0;
                    margin-top: -5px;

                    border-right: none;
                    border-top: 5px solid transparent;
                    border-bottom: 5px solid transparent;
                    border-left: 5px solid $red1;
                }
            }
        }
    }

    &.side-menu--it2 {
        .side-menu-item--2 {
            margin-left: 15px;
        }
    }
}

.side-menu--mobile {
    margin-top: 10px;
    margin-bottom: 20px;
    text-align: center;

    @include breakup-breakpoint('medium') {
        // 768px
        text-align: left;
        //border-right: 3px solid $red1;
        //padding-right: 20px;
    }

    a.side-menu-item--mobile {
        padding: 10px;
        border: none;
        display: block;
        position: relative;

        &::before {
            content: ' ';
            position: absolute;
            width: 0;
            height: 0;

            $arrow-size: 8px;
            bottom: -$arrow-size;
            left: 50%;
            margin-left: -$arrow-size;
            border-left: $arrow-size solid transparent;
            border-right: $arrow-size solid transparent;
            border-top: $arrow-size solid white;
        }

        @include breakup-breakpoint('thinish_extended_2') {
            display: none;
        }

        &:hover span {
            border-bottom: 1px dotted #858687;
        }

        font-size: 1.2em;
        margin-bottom: 10px;
        background: white;
        color: $red1;
        text-decoration: none;

        &:focus {
            background: $red1;
            color: white;

            &::before {
                border-top-color: $red1;
            }

            span {
                border-bottom: 1px solid white;
            }
        }

        &.selected {
            background: $red1;
            color: white;

            span {
                border: none;
            }
            &::before {
                border-top-color: $red1;
            }
        }
    }
}

.side-menu__main-content {
    @include breakup-breakpoint('medium') {
        // 768px
        text-align: left;
        border-left: 3px solid $red1;
        padding-left: 10px;
    }
}

.membership-item-text {
    background-color: white;
    margin-bottom: 20px;
    padding: 10px 5px;
    display: block;

    .grey-row {
        background-color: #e7e7e7;
    }

    table {
        margin-bottom: 20px;
    }

    @include breakup-breakpoint('medium') {
        padding: 20px 20px 60px;
        margin-right: -10px;
    }

    td,
    th {
        text-align: center;
    }
}

.membership-footer-text {
    background-color: white;
    padding: 10px 20px;

    @include breakup-breakpoint('medium') {
        margin: 0 0px 0 0px;
    }
}

.membership-item {
    margin-bottom: 20px;
    background-color: white;
    padding: 10px 5px;

    display: none;
    &.selected {
        display: block;
    }

    .first {
        margin-bottom: 20px;
    }

    .membership-header--bottom,
    .first .membership-classes--text {
        display: none;
    }

    .membership-prices--top {
        float: left;
        width: 120px;
    }

    .membership-classes--comments {
        padding: 0 10px;
    }

    .membership-prices--bottom {
        float: left;
        margin-bottom: 20px;

        p {
            margin-bottom: 0;
        }
    }

    .first .mobile-info::after {
        content: '+ free classes';
    }

    li {
        list-style-type: none;
    }

    ul.membership-prices {
        margin: 0;
    }

    .price {
        color: $red1;
    }

    .membership-separator {
        color: white;
        border-top: 1px solid #e7e7e7;
        margin-top: 50px;
    }

    p {
        margin-top: 0;
        padding-top: 0;
    }

    .member {
        color: $red1;
        font-weight: 600;
        text-align: left;

        @include breakup-breakpoint('thinish') {
            padding-top: 10px;
            padding-left: 10px;
        }
    }

    .second .membership-prices,
    .second .membership-classes {
        padding-left: 20px;
    }

    .display-classes {
        display: block;
        padding-left: 10px;
        font-style: italic;

        .display-mobile {
            display: inline;
        }
    }

    @include breakup-breakpoint('thinish_extended_2') {
        background-color: transparent;
        padding: 0px;
        margin-right: 10px;

        .first {
            margin-bottom: 50px;
        }

        .membership-header--bottom,
        .first .membership-classes--text {
            display: block;
        }

        p {
            margin-top: 0;
            padding-top: 0;
        }

        .membership-prices--top {
            float: none;
        }

        .membership-classes--comments {
            padding: 20px 20px 0;
        }

        .membership-prices--bottom {
            float: none;
            margin-bottom: 0px;

            p {
                margin-bottom: 0;
            }
        }

        .first .membership-prices--bottom::after {
            content: '';
        }

        ul.membership-prices {
            padding-left: 0 !important;
            padding-right: 2px;

            &.last-column {
                padding-right: 0;
            }
        }

        .padding-adjust .membership-header--bottom {
            padding: 0;
            margin: 0;
        }

        .membership-prices--bottom,
        .membership-classes--text {
            background-color: white;
        }

        .membership-prices--bottom {
            margin-left: 0;
            padding: 20px;
        }

        .membership-prices--top {
            padding-left: 20px;
        }

        .membership-prices {
            padding: 0;
        }

        .membership-prices p,
        .membership-header--top p,
        .membership-header {
            margin-top: 0;
        }

        .membership-classes {
            padding: 0;
            margin-top: 2px;

            .membership-classes--text {
                padding: 20px;
            }
        }

        .membership-header--bottom {
            float: right;
            margin-top: 20px;
        }

        .price {
            font-weight: 600;
        }

        .membership-separator {
            display: none;
        }

        .second .membership-prices,
        .second .membership-classes {
            padding-left: 0px;
        }

        .display-classes {
            padding-left: 10px;
            font-style: normal;

            .display-mobile {
                display: none;
            }
        }
    }

    table {
        border-spacing: 1px;
        table-layout: fixed;
        width: 100%;
        word-wrap: break-word;

        td,
        th {
            padding: 0;
        }

        .main {
            color: $red1;
            font-weight: 600;
            text-align: left;
        }

        .label-v {
            text-align: right;
        }

        .second {
            .main,
            .label-h {
                padding-top: 40px;
            }
        }

        .price {
            background: white;
            color: $red1;
            font-weight: 600;
        }
        .text {
            background: white;
        }

        @include breakup-breakpoint('thin') {
            // 320px
            .main {
                padding: 0 10px 5px 0;
            }
            .label-v {
                padding: 10px 10px 10px 0;
            }
            .label-h {
                padding: 0 10px 5px;
            }
            .price,
            .text {
                padding: 10px;
            }
        }
        @include breakup-breakpoint('thinish_extended_2') {
            // 480px
            word-wrap: normal;

            .main {
                padding: 0 20px 10px 0;
            }
            .label-v {
                padding: 20px 20px 20px 0;
            }
            .label-h {
                padding: 0 20px 10px;
            }

            .price,
            .text {
                padding: 20px;
            }
        }
    }
}

