.widget {
    margin-bottom: 10px;
    padding: 15px 10px;

    &.widget-white {
        background: white;
    }

    &.widget-purple {
        background: purple;
        color: white;
    }

    &.widget-about-us {
        background: white;
        padding-bottom: 20px;
    }
}

.widget.book-a-class-widget {
    text-align: center;
    overflow: hidden;
    background-color: #380431;
    color: #ffffff;

    &:hover {
        opacity: 0.9;
    }

    a {
        display: block;
        color: #ffffff;
        text-decoration: none;
        background-color: transparent;

        &:link {
            color: #ffffff;
            border: 0;
        }
        &:visited {
            color: #ffffff;
            border: 0;
        }
        &:hover {
            color: #ffffff;
            border: 0;
        }
        &:focus &:active {
            color: #ffffff;
            border: 1px dotted #999;
        }

        h1:hover,
        h2:hover {
            border: 1px dotted #999;
        }
    }
}
