.connect-with-us {
    a {
        color: #eee;
        text-decoration: none;
        background-color: transparent;
        border: 0;

        &:link {
            color: #eee;
        }
        &:visited {
            color: #eee;
            border-bottom: 1px dotted #ccc;
        }
        &:hover {
            color: $white;
            border-bottom: 2px solid $white;
        }
        &:focus,
        &:active {
            color: #555;
            border: 0;
            background-color: $white;
        }
    }

    .social-icon {
        background: url('i/home/social-white-i.png') no-repeat 0 -30px;
        display: inline-block;
        line-height: 27px;
        height: 27px;
        width: 30px;
        border-bottom: none;
        text-decoration: none;
        margin-right: 10px;

        &:hover,
        &:active,
        &:focus {
            background-position: 0 0;
        }

        &.social-twitter {
            background-position: -32px -29px;

            &:hover,
            &:active,
            &:focus {
                background-position: -32px 0;
            }
        }
        &.social-youtube {
            background-position: -62px -30px;

            &:hover,
            &:active,
            &:focus {
                background-position: -62px 0;
            }
        }
        &.social-linkedin {
            background-position: -94px -31px;

            &:hover,
            &:active,
            &:focus {
                background-position: -94px 0;
            }
        }
        &.social-weibo {
            background-position: -129px -30px;

            &:hover,
            &:active,
            &:focus {
                background-position: -129px 0;
            }
        }
        &.social-youku {
            background-position: -166px -29px;
            width: 78px;

            &:hover,
            &:active,
            &:focus {
                background-position: -166px 0;
            }
        }
        &.social-pininterest {
            background-position: -247px -30px;

            &:hover,
            &:active,
            &:focus {
                background-position: -247px 0;
            }
        }
        &.social-google {
            background-position: -279px -30px;

            &:hover,
            &:active,
            &:focus {
                background-position: -279px 0;
            }
        }

        &.social-instagram {
            background-position: -312px -30px;

            &:hover,
            &:active,
            &:focus {
                background-position: -312px 0;
            }
        }
    }
}
