.hallway {
    margin-bottom: 10px;

    .hallway-item {
        position: relative;
        overflow: hidden;
        margin-top: 10px;
    }

    .image {
        max-width: 100%;

        img {
            margin: 0;
            vertical-align: middle;
            width: 100%;
        }
    }

    .hallway-content {
        background-color: #ffffff;
        padding: 10px;
        //min-height: 250px;
        overflow: hidden;
        margin-bottom: 10px;

        h2,
        h3 {
            margin: 0;
        }
    }
}
