.white-box {
    padding: 5px 30px;
    background: transparent;
    font-size: 18px;
    text-align: center;

    @include breakup-breakpoint('thinish') {
        padding: 0 80px 5px;
    }

    @include section-spacing();
}
.standfirst {
    font-family: ff-meta-web, 'Helvetica Neue', Arial, Helvetica, sans-serif;
    font-size: 18px;
    font-weight: 300;
    line-height: 1.4em;
    margin: 0 0 10px 0;
    color: #000;
}
