.home {
    .content {
        .content-links {
            margin-bottom: 20px;
            color: #ffffff;

            .content-members-links {
                background-color: #dc2e42;
                display: inline-block;
                height: 150px;
                width: 100%;
            }

            .content-corp-links {
                background-color: #09343b;
                display: inline-block;
                height: 150px;
            }

            .content-classes-links {
                background-color: #380431;
                width: 100%;
                display: inline-block;
                height: 250px;
            }

            .content-links--sub {
                width: 100%;
                height: 230px;
                margin-top: 10px;
                text-align: center;
                padding: 20px;
                overflow: hidden;

                &:hover {
                    opacity: 0.9;
                }

                @include breakup-breakpoint(thinish) {
                    height: 230px;
                }

                @include breakup-breakpoint('thinish') {
                    height: 200px;
                }

                @include breakup-breakpoint('thinish_extended_2') {
                    height: 235px;
                    padding: 10px;
                }

                @include breakup-breakpoint('wide') {
                    height: 230px;
                    padding: 20px;
                }

                a {
                    display: block;
                    color: #ffffff;
                    text-decoration: none;
                    background-color: transparent;

                    &:link {
                        color: #ffffff;
                        border: 0;
                    }
                    &:visited {
                        color: #ffffff;
                        border: 0;
                    }
                    &:hover {
                        color: #ffffff;
                        border: 0;
                    }
                    &:focus,
                    &:active {
                        color: #ffffff;
                        border: 0;
                    }

                    h1:hover,
                    h2:hover {
                        border: 1px dotted #999;
                    }
                }

                img {
                    border: none;
                }

                h1,
                h2,
                h3,
                p,
                legend {
                    color: #ffffff;
                }

                h1,
                h2,
                h3 {
                    font-weight: bold;
                }
            }
        }

        .content-txt {
            min-height: 120px;
            height: auto;
            background-color: #ffffff;
            padding: 10px 20px;
            text-align: center;
            margin-left: auto;
            margin-right: auto;
            margin-bottom: 25px;
            font-size: 1.25em;

            @include breakup-breakpoint('medium') {
                padding: 20px 40px;
            }
        }

        .content-video {
            margin-bottom: 30px;
            display: block;
            min-height: 190px;
            background-color: #ffffff;

            .content-video--text {
                width: auto;
                background-color: #ffffff;
                padding: 20px;
            }

            .content-video--iframe {
                iframe {
                    width: 100%;
                    display: block;
                }
            }
        }
    }
}
