@mixin cta($classname) {
    p.#{$classname} a,
    a.#{$classname} {
        @content;
    }
}

@include cta(cta) {
    display: inline-block;
    padding: 5px 10px;

    border: none;

    &:hover {
        span {
            border-bottom: 1px dotted black;
        }
    }

    &:focus,
    &:active {
        background: $red1;
        color: white;
    }
}

@include cta(cta--purple) {
    background: #380431;
    color: white;

    &:hover span {
        border-bottom-color: white;
    }
}
