.prices {
    margin-bottom: 20px;

    .prices--table {
        width: 100%;
        display: block;

        @include breakup-breakpoint('thinish') {
            display: table;
            table-layout: fixed;
            border: 1px solid #e7e7e7;
        }
    }

    .table-row {
        display: block;

        @include breakup-breakpoint('thinish') {
            display: table-row;
        }
    }

    .cell {
        text-align: left;
        border-width: thin;
        border: 0;
        display: block;

        @include breakup-breakpoint('thinish') {
            text-align: center;
            display: table-cell;
            border: 1px solid #e7e7e7;
        }

        span {
            display: inline-block;
        }
    }

    .prices--header--desktop-only {
        display: none;
        font-weight: bold;
        @include breakup-breakpoint('thinish') {
            display: table-row;
        }
    }

    span.add--mobile--public,
    span.add--mobile--city-students,
    span.add--mobile--city-staff,
    span.add--mobile--other-students {
        color: black;
        font-weight: normal;
        display: inline-block;
        margin-left: 10px;

        @include breakup-breakpoint('thinish') {
            margin-left: 0px;

            position: absolute; //screen-reader only
            width: 1px;
            height: 1px;
            margin: -1px;
            padding: 0;
            overflow: hidden;
            clip: rect(0, 0, 0, 0);
            border: 0;
        }
    }

    .prices--class-name {
        text-align: left;
        font-weight: bold;
        margin: auto;

        @include breakup-breakpoint('thinish') {
            text-align: center;
            padding-left: 3px;
            padding-right: 3px;
            font-weight: normal;
        }
    }

    .prices--class-cost {
        padding-left: 10px;
        font-weight: bold;
        color: #b02a1a;

        @include breakup-breakpoint('thinish') {
            padding-left: 0;
        }

        p {
            margin: 0 0 5px 0;
            padding: 0;
        }
    }

    .prices--font--superscript {
        vertical-align: super;
        font-size: 0.7em;
    }
}
