header {
    background-color: #ffffff;
    margin-bottom: 10px;

    h2 {
        font-size: 2.5em;
        font-family: 'ff-meta-web', Arial, Helvetica, sans-serif;
        margin: 10px 0 0 20px;
    }

    .header-logo {
        a {
            text-decoration: none;
            color: #000000;
            border: 0;
            background-color: transparent;

            &:link {
                color: #000000;
            }
            &:visited {
                color: #000000;
            }

            &:hover {
                border-bottom: 1px dotted #999999;
            }

            &:focus,
            &:active {
                border: 1px dotted #999999;
            }
        }

        h2 {
            color: #333333;
            font-weight: 500;
            letter-spacing: -0.5px;
        }

        span.logo-red {
            color: #d81531;
            font-weight: 700;
            letter-spacing: -0.5px;
        }
    }

    .city-member-links {
        display: none;

        @include breakup-breakpoint('thinish') {
            display: inline-block;
            font-weight: 500;
        }

        @include breakup-breakpoint('medium') {
            // 767px
            float: right;
        }

        .city-member-links--sub {
            display: inline-block;
            padding: 10px;
            margin: 10px 0;
            width: 200px;

            p {
                display: inline;
            }

            a {
                width: 100%;
            }

            img {
                margin-right: 5px;
                border: none;
            }
        }

        .member-link {
            background-color: #e5e6e7;
            margin-right: 10px;
            color: #000000;

            .member-links--pointer {
                color: #d31330;
                float: right;
                padding-top: 5px;
            }

            a {
                color: #000000;
                text-decoration: none;
                border: 0;

                &:link {
                    color: #000000;
                }
                &:visited {
                    color: #000000;
                }
                &:hover {
                    color: #000000;
                    text-decoration: none;
                    border-bottom: 1px dotted #000000;
                }
                &:focus,
                &active {
                    background-color: #b91b1b;
                    color: #ffffff;
                    border: 1px dotted #ffffff;
                }
            }
        }

        .class-link {
            background-color: #380431;
            color: #ffffff;

            .class-links--pointer {
                color: #ffffff;
                float: right;
                padding-top: 5px;
            }

            a {
                color: #ffffff;
                text-decoration: none;
                border: 0;

                &:link {
                    color: #ffffff;
                }
                &:visited {
                    color: #ffffff;
                }
                &:hover {
                    color: #ffffff;
                    text-decoration: none;
                    border-bottom: 1px dotted #ffffff;
                }
                &:focus,
                &active {
                    background-color: #b91b1b;
                    color: #ffffff;
                    border: 1px dotted #ffffff;
                }
            }
        }
    }
}
