.hero {
    text-align: center;
    color: $white;
    position: relative;
    margin-bottom: 20px;
    background-color: #333;

    @include breakup-breakpoint(thinish) {
        margin-bottom: 30px;
    }

    @include breakup-breakpoint(medium) {
        margin-bottom: 40px;
    }

    @include section-spacing();

    img {
        width: 100%;
        opacity: 0.5;
        //filter: blur(1px);
    }

    .hero-text {
        @include vertical-align-center();

        h1,
        h2 {
            color: #ffffff;
            font-weight: 600;
            font-size: 1.5em;
            text-shadow: 0px 0px 2.5px #000000;

            @include breakup-breakpoint(thinish) {
                font-size: 2em;
                text-shadow: 0px 0px 1.5px #000000;
            }

            @include breakup-breakpoint(medium) {
                font-size: 3em;
            }
        }
    }

    p {
        font-size: 1.5em;
    }
}
