footer {
    font-family: Arial, sans-serif;
    background-color: #fff;
    margin-top: 20px;
    margin-bottom: 20px;

    .footer-info {
        margin-bottom: 20px;
        background-color: #db2e42;
        color: white;
        padding: 20px;

        a {
            color: #eee;
            text-decoration: none;
            background-color: transparent;
            border: 0;

            &:link {
                color: #eee;
                border-bottom: 1px dotted #ccc;
            }
            &:visited {
                color: #eee;
                border-bottom: 1px dotted #ccc;
            }
            &:hover {
                color: white;
                border-bottom: 2px solid white;
            }
            &:focus,
            &:active {
                color: #555;
                border: 0;
                background-color: white;
            }
        }

        .footer-info--quicklinks {
            margin-bottom: 20px;
            @include breakup-breakpoint('medium') {
                // 768px
                margin-right: 0;
            }
        }

        .city-member-links--sub {
            display: inline-block;
            padding: 10px;
            margin-top: 10px;
            width: 200px;
            max-width: 100%;

            @include breakup-breakpoint('medium') {
                float: right;
            }

            p {
                display: inline;
            }

            a {
                width: 100%;
            }

            img {
                margin-right: 5px;
                border: none;
            }
        }

        .member-link {
            margin-right: 10px;
            color: #000;
            background-color: #e5e6e7;

            @include breakup-breakpoint('medium') {
                // 768px
                margin-right: 0;
            }

            .member-links--pointer {
                color: #d31330;
                float: right;
                padding-top: 5px;
            }

            a {
                color: #000;
                text-decoration: none;
                border: 0;

                &:link {
                    color: #000;
                }
                &:visited {
                    color: #000;
                }
                &:hover {
                    color: #000;
                    text-decoration: none;
                    border-bottom: 1px dotted #000;
                }
                &:focus,
                &active {
                    background-color: #b91b1b;
                    color: #fff;
                    border: 1px dotted #fff;
                }
            }
        }

        .class-link {
            background-color: #380431;
            color: #fff;

            .class-links--pointer {
                color: #fff;
                float: right;
                padding-top: 5px;
            }

            a {
                color: #fff;
                text-decoration: none;
                border: 0;

                &:link {
                    color: #fff;
                }
                &:visited {
                    color: #fff;
                }
                &:hover {
                    color: #fff;
                    text-decoration: none;
                    border-bottom: 1px dotted #fff;
                }
                &:focus,
                &:active {
                    background-color: #b91b1b;
                    color: #fff;
                    border: 1px dotted #fff;
                }
            }
        }

        .footer-social {
            margin-bottom: 20px;
            span.fa {
                padding: 1px;
            }
            a {
                color: #fff;
                text-decoration: none;
                background-color: transparent;
                border-bottom: 0;
                font-size: 2em;
                margin-right: 10px;

                &:link,
                &:visited {
                    color: #fff;
                    border-bottom: 0;
                }
                &:hover {
                    color: #000;
                    border-bottom: 0;
                }
                &:focus,
                &:active {
                    color: #000;
                    border-bottom: 0;
                    background-color: white;
                }
            }
        }
    }

    .footer-links {
        border-bottom: dotted 1px #858687;
        padding-bottom: 10px;
        margin-bottom: 20px;
        font-size: 1.05em;
    }

    .footer-links,
    .footer-terms {
        a {
            color: #858687;
            text-decoration: none;
            border: 0;
            background-color: transparent;

            &:link {
                color: #858687;
            }
            &:visited {
                color: #858687;
            }
            &:hover,
            &:focus {
                color: #858687;
                border-bottom: 1px dotted #858687;
            }
            &:focus {
                outline: thin dotted;
                outline: 5px auto -webkit-focus-ring-color;
            }
            &:active {
                border: 1px dotted #858687;
            }
        }
        span.separator {
            padding-right: 10px;
            margin-right: 10px;
            border-right: 1px solid #858687;
        }
    }

    .footer-copyright {
        color: #858687;
        margin-bottom: 20px;
    }

    .footer-terms {
        color: #858687;

        .footer-termslinks {
            margin-bottom: 20px;
            float: left;

            @include breakup-breakpoint('medium') {
                margin-right: 300px;
            }

            @include breakup-breakpoint('wide') {
                margin-right: 0;
            }
        }
        .footer-logo {
            clear: right;

            @include breakup-breakpoint('wide') {
                float: right;
            }

            a {
                width: 80px;
                display: inline-block;
            }

            a:hover {
                border: none;
            }

            img {
                border: none;
                width: 100%;
            }
        }

        .footer-cr-cul {
            margin-right: 30px;

            @include breakup-breakpoint('wide') {
                float: right;
            }
        }
    }
}
