.timetable {
    width: 100%;
    background: white;
    border-spacing: 0;
    border-bottom: 3px solid #e7e7e7;
    margin-bottom: 20px;
    padding: 20px;

    @include breakup-breakpoint('wide') {
        table-layout: fixed;
    }

    td {
        padding: 0 10px;
        display: block;

        @include breakup-breakpoint('thinish') {
            display: table-cell;
        }
    }

    tr {
        display: block;
        margin-bottom: 20px;

        @include breakup-breakpoint('thinish') {
            margin-bottom: 10px;
            display: table-header-group;
        }

        @include breakup-breakpoint('thinish_extended') {
            display: table-header-group;
        }

        @include breakup-breakpoint('medium') {
            display: table-row;
            margin-bottom: 10px;
        }
    }

    .timetable-day td {
        font-weight: 600;
    }

    .timetable-day h2 {
        margin: 0;
    }

    .timetable-header td {
        display: none;
        padding: 10px;

        @include breakup-breakpoint('thinish') {
            display: table-cell;
        }
    }

    .timetable-class td {
        padding-bottom: 5px;

        @include breakup-breakpoint('thinish') {
            padding-bottom: 15px;
        }

        @include breakup-breakpoint('medium') {
            padding-bottom: 5px;
        }
    }

    .timetable-starttime {
        font-weight: 600;
        margin-right: 10px;
    }

    .timetable-duration {
        font-style: italic;
    }
}
