}

.membership-menu a.membership-menu-item {
    padding: 10px;
    border: none;
    display: block;
    font-size: 1.2em;
    margin-bottom: 10px;
    background: white;
    color: #b02a1a;
    text-decoration: none;
}

@media only screen and (min-width: 600px) {
    .membership-menu a.membership-menu-item {
        display: inline-block;
    }
}

@media only screen and (min-width: 768px) {
    .membership-menu a.membership-menu-item {
        display: block;
    }
}

.membership-menu a.membership-menu-item:hover span {
    border-bottom: 1px dotted #858687;
}

.membership-menu a.membership-menu-item:focus {
    background: #b02a1a;
    color: white;
}

.membership-menu a.membership-menu-item:focus span {
    border-bottom: 1px solid white;
}

.membership-menu a.membership-menu-item.selected {
    background: #b02a1a;
    color: white;
    position: relative;
}

.membership-menu a.membership-menu-item.selected span {
    border: none;
}

.membership-menu a.membership-menu-item.selected::before {
    content: ' ';
    position: absolute;
    width: 0;
    height: 0;
    bottom: -5px;
    left: 50%;
    margin-left: -5px;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    border-top: 5px solid #b02a1a;
}

@media only screen and (min-width: 768px) {
    .membership-menu a.membership-menu-item.selected::before {
        left: auto;
        right: -5px;
        bottom: auto;
        top: 50%;
        margin-left: 0;
        margin-top: -5px;
        border-right: none;
        border-top: 5px solid transparent;
        border-bottom: 5px solid transparent;
        border-left: 5px solid #b02a1a;
    }
}

.membership-menu--mobile {
    margin-bottom: 20px;
