.js .accordion .accordion-content {
    display: none;
}

.accordion {
    margin-bottom: 20px;

    i,
    span.fa {
        margin-right: 5px;
        display: inline-block;
        color: #9a0917;
    }

    .accordion-item {
        background-color: #ffffff;
        margin-bottom: 5px;
        padding: 10px;
        border: 1px dotted transparent;

        &:hover,
        &:focus,
        &:active {
            border: 1px dotted #999;
        }

        .accordion-title {
            a {
                color: #000000;
                border: 0 none;
                text-decoration: none;
                background-color: #ffffff;

                &:link {
                    color: #000000;
                }
                &:visited {
                    color: #000000;
                }
                &:hover {
                    color: #000000;
                    text-decoration: none;
                }
                &:focus {
                    color: #000000;
                    text-decoration: none;
                    background-color: #ffffff;
                }
                &:active {
                    color: #9a0917;
                    text-decoration: none;
                    background-color: #ffffff;
                    border: 1px dotted #999999;
                }
            }
        }

        &.open {
            border: 1px dotted #999;
        }

        &.open .accordion-title a span {
            color: #9a0917;
            border: 0;
            text-decoration: none;
            background-color: #ffffff;
        }

        .accordion-content {
            color: #000000;
            padding-left: 20px;

            p {
                margin-bottom: 5px;
            }
        }
    }
}
