#citysport {
    .main-nav,
    .sec-nav {
        display: none;

        @include breakup-breakpoint('medium') {
            // 768px
            display: block;
        }
    }

    .main-nav {
        font-size: 16px;
        a {
            display: inline-block;
            color: #222;
            font-weight: 600;
            border: 0;

            &:hover span {
                border-bottom: 1px solid #222;
            }

            padding: 5px 20px;
            &.selected,
            &.hier {
                background: #222;
                color: white;

                &:hover span {
                    border-bottom: 1px solid white;
                }
            }

            &:focus,
            &.hier:focus,
            &.selected:focus {
                background: $red1;
                color: white;
                span {
                    border-color: white;
                }
            }

            &:active:focus span {
                border-bottom: 1px solid white;
            }
        }
    }

    .sec-nav {
        background: #222;
        margin-bottom: 1px;

        a {
            display: inline-block;
            color: white;
            font-weight: 600;
            padding: 5px 20px;
            border: 0;
            &.selected,
            &:hover,
            &.hier {
                span {
                    border-bottom: 1px solid white;
                }
            }
        }

        &.parent a.parent span {
            border-bottom: 1px solid white;
        }
    }

    .mobile-nav {
        position: relative;

        .mobile-nav-toggle {
            position: absolute;
            font-size: 30px;
            top: -48px;
            right: 0;
            cursor: pointer;
            padding: 0 10px 3px;
            border-radius: 2px 2px 0 0;
        }

        .mobile-nav-menu {
            background: #eee;
            display: none;
            padding: 0;

            a {
                display: block;
                padding: 10px;
                border: none;
                border-bottom: 1px solid #ddd;
                color: black;

                &.child {
                    padding-left: 30px;
                    font-size: 0.9em;
                }

                &.hier {
                    background: #555;
                    color: white;
                }

                &.selected {
                    background: #333;
                    color: white;
                }

                &,
                &.selected,
                &.hier {
                    &:focus {
                        background: $red1;
                        color: white;
                    }
                }
            }
        }

        &.open {
            margin-bottom: 20px;

            .mobile-nav-toggle {
                background: #eee;
            }
            .mobile-nav-menu {
                display: block;
            }
        }
    }
}
