.felxbox-container,
.flexbox-container {
    display: block;
    @supports (display: flex) {
        display: -webkit-box;
        display: -moz-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        flex-wrap: wrap;
        -webkit-flex-direction: row;
        -moz-flex-direction: row;
        -ms-flex-direction: row;
        flex-direction: row;
    }
}

.clear-1 {
    float: left;
    clear: both;
    @supports (display: flex) {
        float: none;
    }
}

.clear-2 {
    float: left;
    @supports (display: flex) {
        float: none;
    }
}

.clear-0 {
    float: right;
    @supports (display: flex) {
        float: none;
    }
}

.flexbox-columns-2 {
    width: 100%;

    @include breakup-breakpoint('thinish_extended_2') {
        width: 50%;
        display: block;
        @supports (display: flex) {
            display: flex;
            -webkit-justify-content: space-between;
            -ms-flex-pack: justify;
            justify-content: space-between;
        }
    }
}

.flexbox-columns-3 {
    width: 100%;

    @include breakup-breakpoint('thinish_extended_2') {
        width: 33%;
        display: block;
        @supports (display: flex) {
            display: flex;
            -webkit-justify-content: space-between;
            -ms-flex-pack: justify;
            justify-content: space-between;
        }
    }
}

.flexbox-hallway-item {
    margin: 10px;
    display: block;
    position: relative;

    @supports (display: flex) {
        display: flex;
        -webkit-flex-direction: column;
        -moz-flex-direction: column;
        -ms-flex-direction: column;
        flex-direction: column;
        align-content: stretch;
        flex-wrap: nowrap;
        justify-content: flex-start;
        align-items: stretch;
        align-content: stretch;
    }

    &.image img {
        width: 100%;
    }
}

.flexbox-hallway-content {
    background-color: white;
    padding: 5px 15px 15px;
    display: block;
    @supports (display: flex) {
        flex: 1 1 auto;
    }
}
