@mixin vertical-align-center() {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    vertical-align: middle;

    &:before {
        height: 100%;
        content: '';
        display: inline-block;
        vertical-align: middle;
        width: 0;
    }

    .centered-text {
        display: inline-block;
        max-width: 90%;
        margin: 0 auto;
        vertical-align: middle;
    }
}
