a {
    color: #b91b1b;
    border-bottom: 1px dotted #999999;
    text-decoration: none;

    &:link {
        color: #b91b1b;
    }
    &:visited {
        color: #670003;
    }
    &:hover {
        color: #670003;
        text-decoration: none;
        border-bottom: 2px solid #670003;
    }
    &:focus,
    &active {
        text-decoration: none;
        background-color: #b91b1b;
        color: #ffffff;
        border-bottom: 2px solid #ffffff;
    }
}
