.sports-main {
    margin-bottom: 10px;

    .sports-info {
        background-color: #ffffff;
        padding: 10px;
        min-height: 250px;
        overflow: hidden;
        margin-bottom: 20px;

        &.sports-info--img {
            img {
                margin: 0;
                vertical-align: middle;
                width: 100%;
            }
        }
    }
}
