.responsive-map {
    position: relative;
    height: 0;
    overflow: hidden;
    padding-bottom: 150%;

    @include breakup-breakpoint('thinish') {
        // Modify the aspect ratio
        padding-bottom: 75%;
    }

    iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}

.responsive-map-wrapper {
    max-width: 640px;
}
